@use 'base/variables' as v;
@use 'base/mixins' as m;

.principal-pez{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet { 
        flex-direction: row;
        justify-content: center;
    }
}

.texto-descripcion {
    text-align: left;
    margin: 0rem 2.5rem 5rem 2.5rem;
    padding:0 5rem;

}
.tabla-descripcion{
    margin: 0rem 2.5rem;
}

.contenedor-img-pez{
    margin: 2.5rem;
    max-width: 20rem;
    img{
        width: 100%;
    }
    @include m.tablet { 
       max-width: 45rem;
       gap: 5rem;
    }
}

.lista-pez{
    
    li {
        line-height: 1.5;
    }
}

.tabla-descripcion{
    // object-position: center;
    img{
        width: 90%;
        margin: 0 auto;
    }
}

