@use 'base/variables' as v;
@use 'base/mixins' as m;

.header_animal{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 45rem;
    max-height: 45rem;
    padding: 2rem 2rem 0 2rem;
}
@include m.tablet { 
    .header_animal{
        height: 60rem;
        max-height: 60rem;
    }
}

.fondo-header-perro-gato{
    background-image: url('../img/perro_gato/fondo_perro_gato.png');
}
.fondo-header-ganaderia{
    background-image: url('../img/ganaderia/fondo_ganaderia.jpg');
}
.fondo-header-peces{
    background-image: url('../img/peces/fondo-pez1.jpg');
}
.fondo_icon_animal{
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(0,0,0,1) 74%);
}