@use 'base/variables' as v;
@use 'base/mixins' as m;
.header-contenido-registro{
    display: flex;
    justify-content: right;
    padding: 2rem 0;
}
.contendor-formulario{
    padding: 5rem;
    @include m.telefono { 
        width: 70%;
    }
    @include m.tablet { 
        width: 50%;
    }
    p {
        color: v.$primario;
    }
}
.contenedor-fondo{
    background-image: url('../img/alimentacion/fondo-perritus.png');
    background-repeat: no-repeat;
    background-position: center right;
    background-size: 70%;
}
.fondo-footer-registro{
    background-image: url('../img/alimentacion/foot-registro.png');
    background-repeat: no-repeat;
    height: 40rem;

}