@use 'base/variables' as v;
@use 'base/mixins' as m;
.alto-icono {
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.iconos {
    .iconos_animal {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;

        @include m.tablet { 
            display: flex;
            justify-content: space-evenly;
            align-items: flex-end;
        }
        li {
            padding: 2.5rem 0;
        }
    }

}
.icono_animal{
    fill: v.$secundario;
    width: 100%;
    &:hover{
        transform: scale(1.2);
        fill: v.$grisObscuro;
    }
}
.btn-animal{
    border: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    &:active{
        transform: scale(.9);
    }
    &:hover{
        transform: scale(1.2);
    }
}
#perro{
    max-height: 3.5rem;
}
#gato{
    max-height: 3rem;
}
#pollo{
    max-height: 3.2rem;
}
#cerdo{
    max-height: 3.3rem;
}
#vaca{
    max-height: 5rem;
}
#caballo{
    max-height: 5rem;
}
#pez{
    max-height: 1.8rem;
}