@use 'base/variables' as v;
@use 'base/mixins' as m;

.header-contenido-animal{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.animal_logo,
.texto-header {
    width: 60%;
}
.fondo_icon_animal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 5rem;
    padding: 0rem 2.5rem;
    img {
        height: 70%;
    }
    p {
        text-transform: uppercase;
        font-size: 2rem;
    }
}
.texto-header {
    padding: 0rem 2.5rem;
    p {
        font-size: 1.2rem;
        line-height: 1.5;
        margin-top: 2.5rem;
    }
}
.tit_perro_gato{
    margin: 5rem auto;
    p {
        text-transform: uppercase;
        text-align: center;
        font-size: 3rem;
    }
}
