@use 'base/variables' as v;
@use 'base/mixins' as m;
/**clases para producto*/
.imagen-lg {
    display: none;
    @include m.tablet { 
        display: block;
        img {
            width: auto;
            height: 40rem;
        }
    }
}
.contenido-imagen{

    @include m.tablet { 
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: center;
    }
}
.galeria-producto{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    img {
        height: 20rem;
        width: auto;
    }
}

.btn-masinfo {
    display: flex;
    justify-content: center;
    img{
        width: 40%;
        margin-top: 2rem;
    }
}
.contenido-descripcion{
    margin-top: 5rem;
    @include m.tablet { 
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem;
    }
}
.texto-producto {
    .descripcion{
        margin-bottom: 5rem;
    }
}