@use 'base/variables' as v;
@use 'base/mixins' as m;

.bg-patitas {
    background-image: url('../img/ferias/fondo.png');
    background-size: 50%;
}
.all-ferias{
    margin-top: 8rem;
}
.feria-contenedor{
    display: flex;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
}
.card-feria {
    max-width: 25rem;
    .texto{
        padding: 5rem 2.5rem;
    }

}
.title_feria {
    display: flex;
    align-items: flex-end;
    img {
        height: 80%;
        @include m.tablet { 
            height: 60%;
        }
    }
}

.card-feria {
    .imagen {
        position: relative;
        .img-text{
            position: absolute;
            left: 0;
            bottom: 0;
            width: 15rem;
            margin-bottom: -4rem;
        }
    }
}

.img-text-rotar {
    img {
        transform: rotateY(180deg);
    }
}
.marco-img {
    margin: 2.5rem;
    overflow: hidden;
    max-height: 20rem;
    img {
        width: auto;
        height: 20rem;
        object-fit: cover;
        object-position: center;
    }
}
.fig-oval {
    border-radius: 64% 100% 62% 100% / 54% 100% 62% 100%; 
}
.fig-oval-inv {
    border-radius:100% 62% 100% 64%/ 100% 62% 100% 54% ; 
}
.contenedor-img-texto{
    position: relative;
    height: 100%;
    width: 100%;
    p {
        position: absolute;
        color: v.$blanco;
    }
}

.tit-feria {
    top: 20%;
    left: 3rem;
}

.feria-icon{
    height: 25rem;
    width: 25rem;
}

.contenedor-titulo-feria{
    max-height: 15rem;
    margin-top: -5rem;
    img {
        height: 15rem;
        margin: 0 auto;
    }
}