@use 'base/variables' as v;
@use 'base/mixins' as m;

.header_event{
    // position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 45rem;
    max-height: 45rem;
    padding: 2rem 0 0 0;
}
@include m.tablet { 
    .header_event{
        height: 70rem;
        max-height: 70rem;
    }
}

.fondo-header-simposio{
    background-image: url('../img/simposios/simposio.jpg');
}

.fondo-header-feria{
    background-image: url('../img/ferias/bg_feria.png');
    background-size: contain;
    background-position: left;
}
.fondo-header-adopciones{
    background-image: url('../img/adopta/fondo-header-adopta.png');
    background-size: contain;
    background-position: right center;
}
.fondo-header-voluntariado{
    background-image: url('../img/voluntariado/fondo-header-voluntariado.png');
    background-size: contain;
    background-position: right center;
}
.fondo_icon_event{
    width: 6rem;
    height: 6rem;
    background-color: v.$primario;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        max-width: 3rem;
        max-height: 3rem;
        &:hover{
            transform: scale(1.1);
        }
    }
}
.header-contenido-event{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.event-logo-texto {
    display: flex;
    gap: 1rem;
    align-items: center;
    p {
        color: v.$primario;
        text-transform: uppercase;
        font-size: 2rem;
        font-weight: lighter;
    }
}
.evento-logo-derecha {
    align-items: flex-end;
}
.btn-vermas{
    display: flex;
    justify-content: center;
    img{
        width: 60%;
    }
}