@use 'base/variables' as v;
@use 'base/mixins' as m;

.marcas-ganaderia {
    margin: 10rem auto;
}
.contenido-marcas{
    display: flex;
    justify-content: center;
    gap: 9rem;
    flex-wrap: wrap;
    @include m.tablet { 
        gap: 3rem;
    }
}
.marcas {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 10rem;
    .marca-producto {
        display: flex;
        align-items: flex-end;
        height: 80%;
        img {
            height: auto;
            width: 20rem;
        }
    }
    .btn-masinfo {
        height: 20%;
        img {
            width: 100%;
            height: 100%;
        }
    }
    
}

.texto-ganaderia {
    margin: 0 auto;
    padding: 5rem 2.5rem;
    @include m.tablet { 
        padding: 5rem 10rem;
    }
}