@use 'base/mixins' as m;

.galeria-header {
    @include m.grid(8, 1rem);
    @include m.tablet { 
        @include m.grid(24, 1rem);
    };
    
    .img-header {
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            &:hover {
                box-shadow: 0px 0px 15px 3px  rgb(0 0 0 / .35);
                transform: scale(1.1);
                transition: 0.3s;
            }
        }
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(9),
        &:nth-child(10),
        &:nth-child(12){
            display: none;
        }
        &:nth-child(3) {
            grid-column: 1/9;
            grid-row: 1/4;
            img {
                // height: 62rem;
                object-position: center;
            }
        }
        &:nth-child(7) {
            grid-column: 1/6;
            grid-row: 4/7;
            img {
                // height: 62rem;
                object-position: center;
            }
        }
        &:nth-child(8) {
            grid-column: 6/9;
            grid-row: 4/11;
            img {
                // width: 94rem;
                object-position: center;
            }
        }
        &:nth-child(11) {
            grid-column: 1/6;
            grid-row: 7/11;
            img {
                // width: 62rem;
                object-position: center;
            }
        }
        @include m.tablet { 
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(6),
            &:nth-child(9),
            &:nth-child(10),
            &:nth-child(12){
                display: block;
            }
            &:nth-child(1) {
                grid-column: 1/6;
                grid-row: 1/5;
                img {
                    // height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(2) {
                grid-column: 6/9;
                grid-row: 1/8;
                img {
                    // height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(3) {
                grid-column: 9/17;
                grid-row: 1/4;
                img {
                    // height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(4) {
                grid-column: 17/22;
                grid-row: 1/7;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(5) {
                grid-column: 22/25;
                grid-row: 1/4;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(6) {
                grid-column: 1/6;
                grid-row: 5/11;
                img {
                    // height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(7) {
                grid-column: 9/14;
                grid-row: 4/7;
                img {
                    // height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(8) {
                grid-column: 14/17;
                grid-row: 4/11;
                img {
                    // width: 94rem;
                    object-position: center;
                }
            }
            &:nth-child(9) {
                grid-column: 22/25;
                grid-row: 4/11;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(10) {
                grid-column: 6/9;
                grid-row: 8/11;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(11) {
                grid-column: 9/14;
                grid-row: 7/11;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(12) {
                grid-column: 17/22;
                grid-row: 7/11;
                img {
                    // width: 62rem;
                    object-position: center;
                }
            }
            
        }
    }
}