@use 'base/variables' as v;
@use 'base/mixins' as m;


.franja-izq{
    height: 5rem;
    width: 50%;
    margin-top: -5rem;
    border-top: solid transparent 0px;
    border-left: solid transparent 0px;
    border-right: solid transparent 5rem;
    border-bottom:  solid v.$plomoClaro 5rem;
}

.franja-der{
    height: 5rem;
    width: 50%;
    margin-top: -5rem;
    border-top: solid transparent 0px;
    border-left: solid transparent 5rem;
    border-right: solid transparent 0px;
    border-bottom:  solid v.$plomoClaro 5rem;
}
#triangulo-inferior {
    border-top: 2em solid v.$negro;
    border-right: 3.4641em solid transparent;
    border-left: 3.4641em solid transparent;
    height: 0;
    width: 0;
}
.contenedorp {
display: flex;
justify-content: center;
}

.event-title-simposio{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    span{
        margin: 0;
        height: 100%;
        font-family: v.$fuenteSecundaria;
        font-size: 8rem;
        text-transform: uppercase;
        color: v.$blanco;
        // font-weight: 700;
        letter-spacing: .5rem;
        opacity: .7;
        margin-bottom: -.5rem;
    }
    @include m.tablet { 
        span {
            font-size: 10rem;
            letter-spacing: 1rem;
            margin-bottom: -1rem;
        }
    }
}
.titulo_simposio {
    margin: 0 auto;
    padding: 0;
    p {
        text-transform: uppercase;
        text-align: center;
        font-size: 3rem;
    }
}
.simposio-contenedor {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: 0 5rem;
    @include m.tablet { 
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }
    .card-simposio {
        margin-bottom: 5rem;
        .imagen {
            display: flex;
            justify-content: center;
            img {
                width: 30rem;
            }
        }
        .boton {
            margin-top: 2.5rem;
            display: flex;
            justify-content: center;
            img {
                width: 20rem;
            }
        }
    }
}

.contenedor-btn{
    margin-top: 2.5rem;
    display: flex;
    justify-content: flex-end;
    .btn-volver {
        display: flex;
        align-items: center;
        gap: .5rem;
    }
}
#btn-atras{
    fill: v.$negro;
    &:hover{
    fill: v.$primario;
    }
}
.simposio-icon{
    display: flex;
    justify-content: center;
    height: 20rem;
    padding-top: 5rem;
    // margin-bottom: 5rem;
}