@use 'base/variables' as v;
@use 'base/mixins' as m;

.bg_color_knino{
    background-color: #fec200;
}

.bg_img_blancos{
    background-image: url('../img/knino/fondo_lineas.png');
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: center top;
    // opacity:0.3;
    // filter:alpha(opacity=30); /* For IE8 and earlier */
}
.logo_proani {
    height: 10rem;
    img {
        max-height: 10rem;
        margin: 0 auto;
    }
}
.main_contenedor {
    margin-top: 5rem;
    height: 25rem;
    img {
        margin: 0 auto;
        max-height: 25rem;
    }
    @include m.tablet { 
        height: 55rem;
        img {
            margin: 0 auto;
            max-height: 60rem;
        }
    }
}

.productos{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 5rem;
    @include m.tablet { 
        grid-template-columns: repeat(4,1fr);
    }
}

.caja-producto:nth-child(1){
    order: 1;
}
@include m.tablet { 
    .caja-producto:nth-child(1){
        order: unset;
    }   
}
.producto{
    height: 20rem;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
}
@include m.tablet { 
    .producto{
        height: 40rem;
    }
}
.btn-descarga{
    height: 8rem;
    margin: 0 auto;
}
#drago {
    max-width: 75%;
}
#knino {
    max-width: 75%;
}
.seccion-producto{
    margin: 5rem;
}

.detalle-producto{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
}
.producto-slice{
    img{
        max-height: 65rem;
    }   
}
.tabla-nutrientes{
    margin: 0 1rem;
}
.flechader-slice{
    -ms-transform: rotate(180deg); /* IE 9 */
    -webkit-transform: rotate(180deg); /* Chrome, Safari, Opera */
    transform: rotate(180deg);
}
.flechader-slice, 
.flechaizq-slice {
    cursor: pointer;
    img{
        max-height: 5rem;
    }
}
.descripcion {

    p{
        margin: 5rem;
    }
}
.tbl-nutrientes{
    border: 2px solid #2261B4;
    margin-left: auto;
    margin-right: auto;
    border-spacing: 0;
    th, td {
        width: 50%;
        border: 2px solid #2261B4;
        border-spacing: 0;
        padding: 1rem;
    }
    th {
        span{
            color: #2261B4;
            font-size: 2.5rem;
        }
    }
    td {
        span{
            font-weight: bold;
            font-size: 2rem;
        }
    }
}
.tbl-azul{
    border: 2px solid #2261B4;
    th, td {
        border: 2px solid #2261B4;
    }
    th {
        span{
            color: #2261B4;
        }
    }
}

.tbl-white{
    border: 2px solid white;
    th, td {
        border: 2px solid white;
    }
    th {
        span{
            color: white;
        }
    }
}