@use 'base/variables' as v;
@use 'base/mixins' as m;

.eventos{
    ul {
        display: flex;  
        justify-content: center; 
        column-gap: 2rem;
        flex-wrap: wrap;
        li {
            .fondo_icon {
                margin: 3rem 0 1rem 0;                
                width: 10rem;
                height: 10rem;
                // background-color: v.$primario;
                // border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: flex-end; 
                svg {
                    max-width: 6rem;
                    max-height: 6rem;
                }
            }
            p {
                font-family: v.$fuenteSecundaria;
                font-size: 1.6rem;
                text-transform: uppercase;
                text-align: center;
                margin-top: 0;
            }
        }
    } 
    
}
.eventos li {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.ancla_sec{
    &:hover {
        color: v.$primario;
    }
    &.activo {
        color: v.$primario;
    }
}