@use 'base/variables' as v;
@use 'base/mixins' as m;

.form-contacto{
    text-align: left;
    padding: 5rem;
}
#map { 
    height: 30rem;
    width: 100%;
    @include m.tablet { 
        width: 70%;    
        margin: 5rem 0;

    } 
}
.contenedor-ubicaciones{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    @include m.tablet { 
        flex-direction: row;
        gap: 2.5rem;
    }
}
.list-sucursales, .sucursal-select{
    margin: 5rem 0;
    span {
        font-size: 1.5Rem;
        text-transform: uppercase;
        color: v.$primario;
    }
    dl {
        padding-left: 2rem;
        margin-top: 0;
        font-size: 1.2rem;
        font-weight:300;
        dd {
            margin: 0;
            font-style: italic;
        }
    }
}
.sucursal-select{
    margin: 0;
}
.formulario {
    // margin: 2rem;
    .campo{
        margin-bottom: 2rem;
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }
    label {
        text-transform: uppercase;
        display: block;
        font-size: 1.8rem;
        font-weight: 700;
    }
    input:not([type="submit"], [type="radio"], [type="checkbox"]),
        select {
            width: 100%;
            padding: .5rem;
            border: .1rem solid v.$primario;
            border-radius: .5rem;
        }
}
#departamento{
    width: 20rem;
}
.btn-enviar{
    background-color: v.$primario;
    color: v.$blanco;
    border: .1rem solid v.$primario;
    border-radius: .5rem;
    padding: .5rem 1rem;
    cursor: pointer;

}
.center {
    display: flex;
    justify-content: center;
}
.formulario-select{
    max-width: 25rem;
}

.campo.center{
    margin: 5rem 0;
}
.fondo-footer{
    background-image: url(../img/contacto/fondo_foot.png);
    height: 25rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}