/** botones para reproductor de video*/
.button {
    inline-size: 32px;
    block-size: 32px;
    border: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
  }
  
  .button:active{
    transform: scale(.9);
  }
  
  #play{
    background-image: url('../img/btn_video/play.svg');
  }
  #pause{
    background-image: url('../img/btn_video/pause.svg');
  }
  #backward{
    background-image: url('../img/btn_video/menos10.svg');
  }
  #forward{
    background-image: url('../img/btn_video/mas10.svg');
  }

  .btn-icon{
    border: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    &:active{
        transform: scale(.9);
    }
    &:hover{
        transform: scale(1.2);
    }
}