@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    // background-color: v.$primario;
    .contenido-footer {
        padding: 5rem 0 2.5rem 0;
    }
    .copyright {
        text-align: center;
        margin: 0;
        padding: 2rem;
        color: v.$negro;
    }
}

.icon_footer{
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.footer-box {
    // font-family: v.$fuenteCursiva;
    text-align: center;
    margin-bottom: v.$separacion;
    @include m.tablet { 
        margin-bottom: 0;
    }
    h3 {
        font-family: v.$fuenteCursiva;
        margin: 0 0 1rem 0;
        font-size: 2.4rem;
        color: v.$secundario;
    }
    p, a {
        font-size: 2rem;
        margin: 0;
    }
}
