@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:math';

.nav-container{
    background-color: v.$plomo;
    padding: 0;
}

.nav-principal {
    display: flex;
    flex-direction: column;
    text-align: center;
    @include m.tablet { 
        flex-direction: row;
        justify-content: center;
        gap: math.div(v.$separacion, 2);
    }
    a {
        color: v.$negro;
        font-family: v.$fuenteSecundaria;
        font-size: 1.3rem;
        text-transform: uppercase;
        vertical-align: bottom;
        padding: 1rem 0rem;

        @include m.tablet { 
            margin-bottom: 0;
            border-right: 1px solid #000;
            // padding-right: math.div(v.$separacion, 2)
            padding: 1rem 2.5rem;

        }
        @include m.tablet { 
            &:last-of-type {
                border-right:none;
                padding-right:0;
            }
        }
        &:hover {
            color: v.$blanco;
        }
        &.activo {
            color: v.$blanco;
        }
    }

}