@use 'base/variables' as v;
@use 'base/mixins' as m;

.header-videos {
    background-color: v.$negro;
    height: 8rem;
    padding: 1.5rem 0;
    img {
        height: 100%;
        margin: 0 auto;
    }
}
.nav-videos {
    padding: 2rem;
    display: flex;
    justify-content: space-around;
    gap: 2rem;
    a {
        text-transform: uppercase;
        // color: v.$blanco;
        font-size: 1.2rem;
        font-style: normal;
        &:hover{
            color: v.$blanco;
        }
    }
}
.videos{
    margin: 0 5rem;
    padding: 5rem 0;
}
.contenedor-videos{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    @include m.tablet { 
        grid-template-columns: repeat(3, 1fr);
    }
    .fb-video{
        border-radius: .5rem;
        overflow: hidden;
    }
}