.reproductor{
  max-height: 60rem;
}
#video{
  width: 100%;
}
.player{
    position: relative;
    top: 0%;
    left: 0%;
    width: 100%;
    overflow: hidden;
    max-height: 60rem;
  }
  
  .player video {
    inline-size: 100%;
    aspect-ratio: 16/9;
    vertical-align: middle;
    object-fit: cover;
  }
  
  .player-title{
    color: white;
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 2rem;
  }
  .player-overlay h2 {
    font-size: 2rem;
  }
  .player-actions{
    position: absolute;
    inset: 0;
    /**
    cuando es display flex, se puede aplicar propiedades a los hijos
    con justify-content alineacion en el eje principal
    con align-items alineacion en el eje transversal
    **/
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    /* z-index: 2; */
  }
  .player-progress{
    position: absolute;
    inset-block-end: 0;
    inset-inline: 0;
    display: flex;
    padding: 1rem;
  }
  .player-progress input{
    flex:1;
  }
  .player-overlay{
    position: absolute;
    inset: 0;
    opacity: 0;
    transition: .3s opacity;
  }
  .player:hover .player-overlay{
    opacity: 1;
  }